import {
    createStyles,
    Navbar,
    TextInput,
    Code,
    UnstyledButton,
    Badge,
    Text,
    Group,
    ActionIcon,
    Tooltip,
  } from '@mantine/core';
  import { Link } from 'react-router-dom';
  import {
    IconBulb,
    IconUser,
    IconCheckbox,
    IconSearch,
    IconPlus,
    IconSelector,
  } from '@tabler/icons';
  import { UserButton } from '../user-components/UserButton';
  
  const useStyles = createStyles((theme) => ({
    navbar: {
      paddingTop: 0,
    },
  
    section: {
      marginLeft: -theme.spacing.md,
      marginRight: -theme.spacing.md,
      marginBottom: theme.spacing.md,
  
      '&:not(:last-of-type)': {
        borderBottom: `1px solid ${
          theme.colorScheme === 'dark' ? theme.colors.dark[4] : theme.colors.gray[3]
        }`,
      },
    },
  
    searchCode: {
      fontWeight: 700,
      fontSize: 10,
      backgroundColor: theme.colorScheme === 'dark' ? theme.colors.dark[7] : theme.colors.gray[0],
      border: `1px solid ${
        theme.colorScheme === 'dark' ? theme.colors.dark[7] : theme.colors.gray[2]
      }`,
    },
  
    mainLinks: {
      paddingLeft: theme.spacing.md - theme.spacing.xs,
      paddingRight: theme.spacing.md - theme.spacing.xs,
      paddingBottom: theme.spacing.md,
    },
  
    mainLink: {
      display: 'flex',
      alignItems: 'center',
      width: '100%',
      fontSize: theme.fontSizes.xs,
      padding: `8px ${theme.spacing.xs}px`,
      borderRadius: theme.radius.sm,
      fontWeight: 500,
      color: theme.colorScheme === 'dark' ? theme.colors.dark[0] : theme.colors.gray[7],
  
      '&:hover': {
        backgroundColor: theme.colorScheme === 'dark' ? theme.colors.dark[6] : theme.colors.gray[0],
        color: theme.colorScheme === 'dark' ? theme.white : theme.black,
      },
    },
  
    mainLinkInner: {
      display: 'flex',
      alignItems: 'center',
      flex: 1,
    },
  
    mainLinkIcon: {
      marginRight: theme.spacing.sm,
      color: theme.colorScheme === 'dark' ? theme.colors.dark[2] : theme.colors.gray[6],
    },
  
    mainLinkBadge: {
      padding: 0,
      width: 20,
      height: 20,
      pointerEvents: 'none',
    },
  
    collections: {
      paddingLeft: theme.spacing.md - 6,
      paddingRight: theme.spacing.md - 6,
      paddingBottom: theme.spacing.md,
    },
  
    collectionsHeader: {
      paddingLeft: theme.spacing.md + 2,
      paddingRight: theme.spacing.md,
      marginBottom: 5,
    },
  
    collectionLink: {
      display: 'block',
      padding: `8px ${theme.spacing.xs}px`,
      textDecoration: 'none',
      borderRadius: theme.radius.sm,
      fontSize: theme.fontSizes.xs,
      color: theme.colorScheme === 'dark' ? theme.colors.dark[0] : theme.colors.gray[7],
      lineHeight: 1,
      fontWeight: 500,
  
      '&:hover': {
        backgroundColor: theme.colorScheme === 'dark' ? theme.colors.dark[6] : theme.colors.gray[0],
        color: theme.colorScheme === 'dark' ? theme.white : theme.black,
      },
    },
  }));
  
  const links = [
     { href: "/", icon: IconUser, label: 'Hjem' },
     { href: '/local', icon: IconBulb, label: 'Lokallet ditt', notifications: 3 },
    { href: "/trending", icon: IconCheckbox, label: 'Alle Bedrifter', notifications: 4 },
   
  ];
  
  const collections = [
      { emoji: '', label: 'Om Oss', href: '/about' },
     { emoji: '.', label: 'Faq', href: '/faq' },
     { emoji: '.', label: 'Privacy Policy', href: '/privacy_policy' },
     { emoji: '.', label: 'Terms and Conditions', href: '/terms_conditions' },
    // { emoji: '👍', label: 'Sales' },
    // { emoji: '🚚', label: 'Deliveries' },
    // { emoji: '💸', label: 'Discounts' },
    // { emoji: '💰', label: 'Profits' },
    // { emoji: '✨', label: 'Reports' },
    // { emoji: '🛒', label: 'Orders' },
    // { emoji: '📅', label: 'Events' },
    // { emoji: '🙈', label: 'Debts' },
    // { emoji: '💁‍♀️', label: 'Customers' },
  ];
  
  export function NavbarSearch() {
    const { classes } = useStyles();
  
    const mainLinks = links.map((link) => (
      <Link key={link.label} className={classes.mainLink} style={{ textDecoration : "none" }} to={link.href}>
        <div className={classes.mainLinkInner}>
          <link.icon size={20} className={classes.mainLinkIcon} stroke={1.5} />
          <span>{link.label}</span>
        </div>
        {link.notifications && (
          <Badge size="sm" variant="filled" className={classes.mainLinkBadge}>
            {link.notifications}
          </Badge>
        )}
      </Link>
    ));
  
    const collectionLinks = collections.map((collection) => (
      <a
        href={collection.href}
      
        key={collection.label}
        className={classes.collectionLink}
      >
        <span style={{ marginRight: 9, fontSize: 16 }}>{collection.emoji}</span> {collection.label}
      </a>
    ));
  
    return (
      <Navbar height="100vh" width="100vh" p="md" className={classes.navbar}>
       
        <Navbar.Section className={classes.section}>
          <UserButton
            image="https://i.imgur.com/fGxgcDF.png"
            name="Bob Rulebreaker"
            email="Product owner"
            icon={<IconSelector size={14} stroke={1.5} />}
          />
        </Navbar.Section>
  
        <TextInput
          placeholder="Search"
          size="xs"
          icon={<IconSearch size={12} stroke={1.5} />}
          rightSectionWidth={70}
          rightSection={<Code className={classes.searchCode}>Ctrl + K</Code>}
          styles={{ rightSection: { pointerEvents: 'none' } }}
          mb="sm"
        />
  
        <Navbar.Section className={classes.section}>
          <div className={classes.mainLinks}>{mainLinks}</div>
        </Navbar.Section>
  
        <Navbar.Section className={classes.section}>
          <Group className={classes.collectionsHeader} position="apart">
            <Text size="xs" weight={500} color="dimmed">
              Collections
            </Text>
            <Tooltip label="Create collection" withArrow position="right">
              <ActionIcon variant="default" size={18}>
                <IconPlus size={12} stroke={1.5} />
              </ActionIcon>
            </Tooltip>
          </Group>
          <div className={classes.collections}>{collectionLinks}</div>
        </Navbar.Section>
      </Navbar>
    );
  }